.switch {
  width: 40px;
  height: 20px;
  margin: 0 5px;
  display: flex;
  position: relative;
}

.switch__input {
  height: 0;
  width: 0;
  opacity: 0;
}

.switch__slider {
  cursor: pointer;
  background: linear-gradient(180deg, var(--color-blue) 0%, #dcdcdc 100%);
  border: .604701px solid var(--color-blue);
  border-radius: 20px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  cursor: pointer;
  width: 16px;
  height: 16px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1.5px;
  left: 1px;
}

.switch__input:checked + .switch__slider {
  background: linear-gradient(180deg, var(--color-blue) 0%, #686868 100%);
}

.switch__input:checked + .switch__slider:before {
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  transform: translateX(21px);
}

