.list {
  list-style: none;
}

.link {
  // display: block;
  text-decoration: none;
}

.header {
  background-color: var(--color-header-background);
  border: 1.5px solid var(--color-header-border);
  border-radius: 0px 0px 8px 8px;
  position: sticky;
  top: 0px;
  z-index: 1;
}

.mobile.mobile-is-hidden {
  display: none;
}

.header-container {
  display: flex;
  padding: 18px 20px;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    padding: 14px 32px;
  }
}

.header-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  // width: 86px;
  // height: 28px;
  fill: #686868;
}
.logo__link {
  width: 86px;
  height: 28px;
}
.nav-list {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    gap: 16px;
    margin-left: 32px;
    align-items: center;
  }
}

.nav-link {
  display: block;
  padding: 8px 16px;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: -0.04em;
  text-transform: uppercase;
  border-radius: 36px;
  color: var(--color-text-primary);

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: var(--color-yellow);
  }
}

.nav-link-current {
  background-color: var(--color-yellow);
}

.header-options {
  display: flex;
}

.switch {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 20px;
  margin: 0 5px;
  display: flex;
}

.switch__input {
  height: 0;
  width: 0;
  opacity: 0;
}

.switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 20px;
  background: linear-gradient(180deg, var(--color-blue) 0%, #dcdcdc 100%);
  // box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
  border: 0.604701px solid var(--color-blue);
  transition: 0.4s;
}

.switch__slider::before {
  content: '';
  position: absolute;
  cursor: pointer;
  left: 1px;
  // top: 1px;
  bottom: 1.5px;
  // right: 1px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: linear-gradient(180deg, #ffffff 0%, #f7f7f7 100%);
  // box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), 0px 2px 2px rgba(0, 0, 0, 0.1), 1px 2px 4px rgba(0, 0, 0, 0.1);
  transition: 0.4s;
}

.switch__input:checked + .switch__slider {
  background: linear-gradient(180deg, var(--color-blue) 0%, #686868 100%);
  // box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.switch__input:checked + .switch__slider::before {
  background: linear-gradient(180deg, #ffffff 0%, #f7f7f7 100%);
  // box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), 0px 2px 2px rgba(0, 0, 0, 0.1), 1px 2px 4px rgba(0, 0, 0, 0.1);

  transform: translateX(21px);
  -webkit-transform: translateX(21px);
  -ms-transform: translateX(21px);
}

