.login-btn {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 184px;
    height: 48px;
    margin-left: 24px;
    padding: 14px;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    background: #4f2ee8;
    border: none;
    border-radius: 36px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.04em;

    &:hover {
